.section {
    width: 90vw;
    padding: 0 20px;
    margin: 0 auto;
}

@media only screen and (min-width: 1280px) {
    .section {
        width: 1280px;
        padding-left: 16px;
        padding-right: 16px;
        margin-left: auto;
        margin-right: auto;
    }
}

main {

    border: 1px solid grey;
    border-radius: 6px;

    padding: 10px;
}

.header {

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    height: 40px;
    padding: 5px 20px;
  

    border: 2px solid black;
    border-radius: 6px;
    box-shadow: 0 2px 2px gray;

}

.nav {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    gap: 20px;
    

    padding: 0 5px;

    border-left: 1px solid black;
    border-right: 1px solid black;
}

.link {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

    width: 65px;
    height: 30px;

    color: blue;
    text-decoration: none;
    border-radius: 8px;
    transition: background-color 500ms;
}

.link:hover {
    color: blue;
    background-color: rgba(166, 96, 175, 0.404);
    text-decoration: none;
}

.link:active {
    color: blue;

    border-radius: 8px;
    background-color: rgba(166, 96, 175, 0.404);
    text-decoration: none;
}

.auth {
    display: flex;
    flex-direction: row;
    justify-content: space-between; 

    gap: 10px;
}

img {
    width: 100%;
}

.clipboard {
   display: flex;
   align-items: center;
   justify-content: center;

   width: 35px;
   height: 35px;

   border: none;
  
   cursor: pointer;
   border-radius: 8px;
   box-shadow:0px 0px 4px 5px #d1d1d1;
   transition: background-color 500ms;
}

.clipboard:hover {
    background-color: rgba(166, 96, 175, 0.404);
}

.clipboard:active {
    
    box-shadow: inset 0px 0px 4px 5px #d1d1d1;
}

.about {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;

    gap: 10px;
}

@media only screen and (min-width: 320px) and (max-width: 768px) {
    .about {
       
        flex-direction: column;

    }

    .auth {
        display: flex;
        flex-direction: row;
        justify-content: space-between; 
    
        gap: 5px;
    }
}

.userAndOut {

    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;

    gap: 20px;
    
    
}

.userData {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;

    padding: 3px;
    border-radius: 8px;
   
    gap: 5px;

    box-shadow: 0px 0px 4px 5px #d1d1d1;
}