.lable {
   
    text-align: left;
    margin-bottom: 10px;
}

.text {
    margin: 0;
}

.button {
    width: 50%;
    height: 30px;

    font-size: 20px;
    border: none;
    border-radius: 8px;
    margin: 10px 0 10px 0;

    background-color: white;
    /* box-shadow: 0px 0px 22px 0 rgb(212, 212, 212), 0px 0px 12px 0.5px rgb(104, 104, 104), 0px 0px 5px 0 rgb(65, 65, 65); */
    transition: background-color 500ms;
    box-shadow:0px 0px 4px 5px #d1d1d1;
}

.button:hover {
    background-color: rgba(166, 96, 175, 0.404);
}

.button:active {
    box-shadow: inset 0px 0px 4px 5px #d1d1d1;
}

.form {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;

    padding: 10px;
    width: 50%;

    border: 2px solid black;
    border-radius: 8px;
}


.lab {
    width: 100%;
    height: 30px;
    
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    
    gap: 5px;

    margin: 0 auto 0 0;
}

@media only screen and (min-width: 320px) and (max-width: 427px) {
   

    form {
        
        margin: 0;
        width: 100%;
    }

    
    .lab {

        height: 50px;
        display: block;
 
    }

}


@media only screen and (min-width: 428px) {
   

    form {
        margin: 0;
    }

    .fise {

        display: flex;
        justify-content: center;
        max-width: 100%;
        padding-left: 16px;
        padding-right: 16px;
        margin-left: auto;
        margin-right: auto;
    }
}


@media only screen and (min-width: 678px){
    .fise {

        justify-content: flex-end;
        margin-right: 10px;
    }
}

.field {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;

    gap: 10px;
    
}


.in {
    font-size: 18px;
}